@import url(https://fonts.googleapis.com/css?family=Roboto|Roboto+Condensed|Roboto+Mono);
* {
  box-sizing: border-box; }

html,
body {
  height: 100%;
  margin: 0;
  padding: 0; }

html {
  font-size: 62.5%; }

body {
  background-color: #F4F4F4;
  color: #000;
  flex-grow: 1;
  flex-shrink: 0;
  font-family: "Roboto", Helvetica, Arial, sans-serif;
  font-size: 1.6rem;
  line-height: 1.414;
  overflow-y: scroll; }

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Roboto Condensed", sans-serif;
  font-weight: normal; }

h1 {
  font-size: 2.8284em; }

h2 {
  font-size: 2.3784em; }

h3 {
  font-size: 2em; }

h4 {
  font-size: 1.6818em; }

h5 {
  font-size: 1.4142em; }

h6 {
  font-size: 1.1892em; }

input {
  background-color: #FCFCFC;
  border: none;
  border-radius: 0.4rem;
  font-family: "Roboto Mono", monospace;
  font-size: inherit;
  line-height: inherit;
  padding: 2rem; }
  input:focus {
    background-color: #FFF;
    box-shadow: 0 0.2rem 0.4rem #5C6B86;
    outline: none; }

button {
  background-color: #FCFCFC;
  border: 0.2rem solid #7A828F;
  border-radius: 0.4rem;
  color: #415881;
  font-family: "Roboto Mono", monospace;
  font-size: inherit;
  font-weight: bold;
  line-height: inherit;
  padding: 1.8rem; }
  button:hover, button:focus {
    background-color: #FFF;
    border-color: #5C6B86;
    border-bottom-width: 0.3rem;
    border-top-width: 0.1rem;
    outline: none; }
  button:active {
    border-width: 0.2rem; }

.Content {
  display: grid;
  grid-template-columns: [wide-start] 2rem [normal-start] 1fr [normal-end] 2rem [wide-end];
  padding: 2rem 0;
  grid-row-gap: 2rem;
  row-gap: 2rem; }
  @media (min-width: 48rem) and (max-width: 59.999rem) {
    .Content {
      grid-template-columns: [wide-start] 1fr [normal-start] 70rem [normal-end] 1fr [wide-end]; } }
  @media (min-width: 60rem) and (max-width: 74.999rem) {
    .Content {
      grid-template-columns: [wide-start] 1fr [normal-start] 90rem [normal-end] 1fr [wide-end]; } }
  @media (min-width: 75rem) {
    .Content {
      grid-template-columns: [wide-start] 1fr [normal-start] 110rem [normal-end] 1fr [wide-end]; } }

.EmojinateForm {
  align-items: flex-end;
  display: grid;
  grid-gap: 2rem;
  gap: 2rem;
  grid-column: normal;
  grid-template: "emoji " "space " "text  " "submit" /  1fr; }
  @media (min-width: 30rem) {
    .EmojinateForm {
      grid-template: "emoji  space " "text   submit" /  1fr    1fr; } }
  @media (min-width: 60rem) {
    .EmojinateForm {
      grid-template: "emoji space text submit" /  1fr   1fr   1fr  auto; } }
  .EmojinateForm_emoji {
    grid-area: emoji; }
  .EmojinateForm_space {
    grid-area: space; }
  .EmojinateForm_text {
    grid-area: text; }
  .EmojinateForm_submit {
    grid-area: submit; }

.Footer {
  background-color: #5C6B86;
  border-top: 0.2rem solid #7A828F;
  color: #FFF;
  display: grid;
  font-size: 0.8409em;
  grid-template-columns: [wide-start] 2rem [normal-start] 1fr [normal-end] 2rem [wide-end];
  padding: 2rem 0;
  grid-row-gap: 2rem;
  row-gap: 2rem; }
  @media (min-width: 48rem) and (max-width: 59.999rem) {
    .Footer {
      grid-template-columns: [wide-start] 1fr [normal-start] 70rem [normal-end] 1fr [wide-end]; } }
  @media (min-width: 60rem) and (max-width: 74.999rem) {
    .Footer {
      grid-template-columns: [wide-start] 1fr [normal-start] 90rem [normal-end] 1fr [wide-end]; } }
  @media (min-width: 75rem) {
    .Footer {
      grid-template-columns: [wide-start] 1fr [normal-start] 110rem [normal-end] 1fr [wide-end]; } }
  .Footer * {
    grid-column: normal; }

.Header {
  background-color: #5C6B86;
  border-bottom: 0.2rem solid #0A2E70;
  color: #FFF;
  display: grid;
  grid-template-columns: [wide-start] 2rem [normal-start] 1fr [normal-end] 2rem [wide-end];
  grid-row-gap: 2rem;
  row-gap: 2rem; }
  @media (min-width: 48rem) and (max-width: 59.999rem) {
    .Header {
      grid-template-columns: [wide-start] 1fr [normal-start] 70rem [normal-end] 1fr [wide-end]; } }
  @media (min-width: 60rem) and (max-width: 74.999rem) {
    .Header {
      grid-template-columns: [wide-start] 1fr [normal-start] 90rem [normal-end] 1fr [wide-end]; } }
  @media (min-width: 75rem) {
    .Header {
      grid-template-columns: [wide-start] 1fr [normal-start] 110rem [normal-end] 1fr [wide-end]; } }
  .Header > * {
    grid-column: normal; }

.Output {
  background-color: #FFF;
  font-family: "Roboto Mono", monospace;
  grid-column: normal;
  margin: 0;
  overflow: auto;
  padding: 2rem; }

.Page {
  display: grid;
  grid-template: "header " -webkit-min-content "content" 1fr "footer " -webkit-min-content /  1fr;
  grid-template: "header " min-content "content" 1fr "footer " min-content /  1fr;
  min-height: 100%; }
  .Page_header {
    grid-area: header; }
  .Page_content {
    align-self: flex-start;
    grid-area: content; }
  .Page_footer {
    grid-area: footer; }

.Root {
  height: 100%; }

.TextField {
  display: grid;
  grid-gap: 1rem;
  gap: 1rem;
  grid-template: "label" "input"; }
  .TextField_label {
    color: #333;
    grid-area: label; }
  .TextField_input {
    grid-area: input; }

