* {
  box-sizing: border-box;
}

html,
body {
  height: 100%;
  margin: 0;
  padding: 0;
}

html {
  font-size: 62.5%;
}

body {
  background-color: $grayF4;
  color: $black;
  flex-grow: 1;
  flex-shrink: 0;
  font-family: $sans;
  font-size: 1.6rem;
  line-height: 1.414;
  overflow-y: scroll;
}
